/*********** NAVIGATION  ***********/

.main-nav {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  list-style: none;
  background-color: #4e4d4a;
  -ms-flex-align: center;
      align-items: center;
  height: 90px;
}

.main-nav a {
  display: block;
  text-decoration: none;
}

.logo-nav {
  display: -ms-flexbox;
  display: flex;
  font-family: Oswald, sans-serif;
  width: 250px;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  font-size: 1.5em;
}

.logo-nav i,
a {
  height: 30px;
  color: #d26c22;
}

.logo-nav a {
  pointer-events: none;
}

.sub-nav {
  display: grid;
  font-family: "Arial Narrow";
  grid-gap: 10px;
  list-style: none;
  text-align: center;
  grid-template-columns: repeat(8, 1fr);
}

.sub-nav a {
  padding: 3.5px;
  font-size: 1em;
  color: white;
  display: inline;
}

.sub-nav a:hover {
  background: #d26c22;
}

/*********** MAIN  ***********/

.main {
  position: relative;
  height: 40vh;
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  background-image: url("../src/assets/los-angeles.jpg");
  opacity: 0.8;

  /* For text formatting */
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  color: white;
  text-align: center;
  font-family: "Arial Narrow";
}

.main-font {
  font-size: 2.1rem;
  margin-bottom: -10px;
}

.main-subfont {
  font-size: 1.1rem;
}

.main p:hover {
  cursor: default;
}

/*********** TITLE  ***********/

.title {
  font-family: "Arial Narrow";
  font-weight: bold;
  text-align: center;
  color: #d26c22;
  font-size: 2rem;
}

.title p:hover {
  cursor: default;
}

/*********** FORM  ***********/

.form {
  font-family: "Arial Narrow";
  text-align: center;
  margin: 30px;
}

input {
  margin: 10px;
  padding: 8px 25px;
  font-size: 0.8rem;
  border-radius: 4px;
  border-color: #d26c22;
}

button {
  background-color: #d26c22;
  border: none;
  border-radius: 4px;
  color: white;
  padding: 10px 35px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  letter-spacing: 0.09em;
  font-size: 0.8rem;
  font-weight: bold;
  margin: 4px 2px;
  cursor: pointer;
}

/*********** WEATHER  ***********/

.weather {
  text-align: center;
  color: grey;
  font-family: Oswald, sans-serif;
  font-size: 1.1rem;
}

/*********** MEDIA QUERIES  ***********/

@media (max-width: 768px) {
  .sub-nav li {
    float: left;
  }
  .sub-nav li a {
    /* padding: 20px 30px; */
    clear: none;
    float: right;
    max-height: none;
    display: none;
  }

  .main {
    background-size: 120%;
    background-position: center;
  }

  .main-font {
    font-size: 1.8rem;
  }

  .main-subfont {
    font-size: 0.8rem;
  }
}

@media (max-width: 690px) {
  .main {
    background-size: 140%;
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
  }
}



